
import { character } from '../constants/character';
import skill from '../constants/skill';
import item from '../constants/item';
import  memoria  from '../constants/memoria';
import { characterUtils, skillUtils } from '../constants/constantsUtils';

const getStatusMax = (data) => {

  const maxValues = {
    HP: 0,
    素早さ: 0,
    物攻: 0,
    物防: 0,
    魔攻: 0,
    魔防: 0,
  };

  for (const dataEl of Object.values(data)) {
    for (const key in dataEl.status) {
      if (maxValues[key] < dataEl.status[key]) {
        maxValues[key] = dataEl.status[key];
      }
    }
  }

  return (maxValues);  // 各ステータスの最高値を表示します



}

const getSkillStatusMax = (skill) => {

  const maxSkillStatus = {};
  const skillArray = Object.values(skill);
  const keys = ['damage', 'break', 'heal', 'wait']

  //配列最初の値から比較していく
  const firstSkill = skillArray[0];
  for (const key of keys) {
    maxSkillStatus[key] = firstSkill[key] ?? 0;
  }
  for (let i = 1; i < skillArray.length; i++) {
    for (const key of keys) {
      if (!skillArray[i][key]) continue
      if (skillArray[i][key] > maxSkillStatus[key]) {
        maxSkillStatus[key] = skillArray[i][key];
      }
    }
  }

  return (maxSkillStatus);  // 各ステータスの最高値を表示します

}

const getCurrentSkillStatusMax = () => {

  skillUtils(skill)


  // const maxSkillStatus = {
  //   'currentDamage': 0,
  //   'currentBreak': 0,
  //   'currentHeal': 0,
  //   'wait': 0
  // };
  const maxSkillStatus = {
    'damage': 0,
    'break': 0,
    'heal': 0,
    'wait': 0
  };



  for (const skillEl of Object.values(skill)) {
    for (const key of Object.keys(maxSkillStatus)) {
      if (!skillEl[key]) continue
      if (skillEl[key] > maxSkillStatus[key]) {
        maxSkillStatus[key] = skillEl[key];
      }
    }

  }
  return (maxSkillStatus);

}




const skillMaxValues = getSkillStatusMax(skill)
export const maxValues = {
  character: { ...getStatusMax(character), ...skillMaxValues },
  skill: { ...getCurrentSkillStatusMax() },
  memoria: { ...getStatusMax(memoria) },
  item:{...getStatusMax(item)},
}


export const updateSkillStatusMax = () => {

  // maxValues.skill = {
  //   'currentDamage': 0,
  //   'currentBreak': 0,
  //   'currentHeal': 0,
  //   'wait': 0
  // };
  maxValues.skill = {
    'damage': 0,
    'break': 0,
    'heal': 0,
    'wait': 0
  };

  for (const skillEl of Object.values(skill)) {
    for (const key of Object.keys(maxValues.skill)) {
      if (!skillEl[key]) continue
      if (skillEl[key] > maxValues.skill[key]) {
        maxValues.skill[key] = skillEl[key];
      }
    }
  }

}
